* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background-color: #111;
  color: rgb(211, 211, 211);
  font-size: 18px;
  line-height: 1.5;
}

.container {
  max-width: 950px;
  margin: 0 auto;
  padding: 0 30px;
}

.btn {
  display: inline-block;
  margin-top: 20px;
  background-color: rgb(50, 84, 116);
  padding: 10px 25px;
  border: none;
  color: rgb(211, 211, 211);
  cursor: pointer;
  border-radius: 5px;
  font-size: 1.1rem;
  transition: transform 0.3s;
}

.btn:hover {
  background-color: rgb(43, 67, 88);
  transform: scale(0.97);
}

img {
  width: 30%;
  max-width: 100%;
  border-radius: 10px;
}
span {
  color: rgb(76, 144, 208);
}
.navbar {
  background-color: #111;
  height: 70px;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 10;
}

.navbar .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
}

.logo {
  font-size: 1.4rem;
  font-weight: 700;
  color: rgb(60, 168, 128);
  font-family: 'Gill Sans', 'Gill Sans MT', 'Trebuchet MS', sans-serif;
}

.link {
  margin-left: 20px;
  background-color: transparent;
  border: none;
  color: #fff;
  font-size: 1.1rem;
  cursor: pointer;
}

.link:hover {
  color: rgb(164, 163, 163);
}

.nav-menu {
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
  display: none;
  position: absolute;
  top: 35px;
  right: 10px;
}
/* Hero */
.hero {
  margin-top: 70px;
  min-height: 90vh;
  height: auto;
  background-color: #111;
}

.hero .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 90vh;
}

.hero h3, p {
  color: rgb(164, 163, 163);
}

/* About */
.about {
  min-height: 400px;
  height: auto;
  padding: 10px;
  background-color: #111;
  font-size: 0.97rem;
  margin-top: 20px;
}

.about h3{
  text-align: center;
  margin-bottom: 40px;
  font-size: 1.3rem;
}

.about p{
  max-width: 80%;
}

.about .grid-container{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
  align-items: center;
  justify-content: space-between;
}

.progress {
  background-color: rgb(211, 211, 211);
  height: 20px;
  border-radius: 30px;
  width: 100%;
  margin-bottom: 20px;
}

.progress-bar {
  background-color: rgb(72, 126, 176);
  height: 20px;
  border-radius: 30px;
  width: 90%;
  font-size: 0.85rem;
  padding-left: 20px;
  /* border: none; */
}

/* Projects */
.projects {
  min-height: 600px;
  height: auto;
  position: relative;
  background-color: #111;
  margin-top: 20px;
}

.projects h3{
  text-align: center;
  margin-bottom: 40px;
}

.card {
  height: 200px;
  width: 300px;
  background-color: rgb(28, 53, 89);
  border-radius: 10px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.card p {
  color: #fff;
  font-size: 0.8rem;
  text-align: center;
  padding-bottom: 10px;
}

.projects .grid-container {
  display: grid;
  grid-template-columns: 33.33% 33.33% 33.33%;
  gap: 20px;
  justify-content: center;
}

/* Contact */
.contact {
  height: auto;
  padding: 30px;
  min-height: 400px;
  background-color: #111;
  margin-top: 20px;
}

.contact h3 {
  text-align: center;
  margin-bottom: 60px;
}

.contact p {
  text-align: center;
  margin-bottom: 20px;
}

.form-controls {
  text-align: center;
}

.contact input[type=text], input[type=email]{
    outline: none;
    width: 40%;
    padding: 10px;
    margin-bottom: 10px;
    font-size: 1.1rem;
    border-radius: 4px;
    border: none;
}

.contact textarea {
    height: 200px;
    outline: none;
    border-radius: 4px;
    font-size: 1.1rem;
    border: none;
    resize: none;
    width: 40%;
    padding-left: 10px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    overflow: auto;
}

.contact input[type=submit] {
    padding: 10px 20px;
    width: 40%;
    border-radius: 4px;
}

.footer {
  background-color: rgb(129, 127, 127);
  height: auto;
  padding: 20px;
}

.footer p {
  color: rgb(21, 20, 20);
  text-align: center;
}

@media screen and (max-width: 1000px) {
.projects .grid-container {
  grid-template-columns: 50% 50%;
  justify-items: center;
}
}

@media screen and (max-width: 600px) {
  .navbar {
    height: 90px;
  }
  .logo {
    margin-top: 10px;
    user-select: none;
  }
  .nav-links{ 
    display: none;
    margin-bottom: 20px;
    padding-right: 20px;
  }

  .nav-links.active {
    display: block;
  }

.nav-menu {
  display: block;
}
.link {
  color: #fff;
  font-size: 0.97rem;
}
.link:hover {
  color: #fff;
}

.navbar .container {
  flex-direction: column;
}
img {
    width: 90%;
    border-radius: 30px;
    padding-bottom: 20px;
}
.hero {
  margin-top: 100px;
}
.hero .container {
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}
.hero h1 {
  font-size: 2rem;
}
.hero h3 {
  font-size: 1.2rem;
}
.hero p {
  font-size: 1.1rem;
}
.hero img {
  max-width: 100%;
}
.projects .grid-container {
  grid-template-columns: 100%;
}
.about p{
  text-align: center;
  max-width: 100%;
}
.about .grid-container{
  grid-template-columns: 1fr;
}
.contact textarea, input[type=text], input[type=email] {
  width: 100%;
}

.contact input[type=text], input[type=email]{
  width: 100%;
}
}
